.timer {
    font-size: 40pt;
}

.timer h1 {
    margin: 0;
    /* display: inline-block; */
    text-align: center;
    color: #323232;
}
.container {
    display: flex;
    width: 30%;
}

.stopwatch_main {
    text-align: center;
    width: 50%;
    margin: 0 auto;
    padding-top: 4rem;
}

.input_buttons {
    display: flex;
    justify-content: space-evenly;
    margin: 0 auto;
}
.timerMain {
    margin: 0 auto;
    padding-top: 4rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.timer_input {
    display: flex;
    justify-content: center;
    margin: 0 auto;
}

.num_input {
    margin-right: 0.5rem;
}

.controls {
    display: flex;
    flex-direction: column;
}

.timer_style {
    margin: 0 auto;
    padding: 0 2rem;
}


